<template>
  <section class="search-results uk-section">
    <div class="uk-container">
      <div class="results-sort">
        <h5>ترتيب النتائج حسب:</h5>
        <div class="sort-label">
          <label>التوقيت</label>
          <select class="uk-select">
            <option>الأحدث</option>
            <option>الأقدم</option>
          </select>
        </div>
        <div class="sort-label">
          <label>السعر</label>
          <select class="uk-select">
            <option>الأقل أولا</option>
            <option>الأعلي أولا</option>
          </select>
        </div>
        <div class="sort-label">
          <label>المساحة</label>
          <select class="uk-select">
            <option>الأقل أولا</option>
            <option>الأكبر أولا</option>
          </select>
        </div>
      </div>
      <div class="results-view uk-grid-small uk-child-width-1-2@s uk-child-width-1-3@xl" uk-grid>
        <div v-for="result in results" :key="result.aqarId">
          <div class="uk-card uk-card-default uk-card-body">
            <img :src="result.aqarImage.imageUrl">
            <div class="overlay">
              <div class="details">
                <p class="date">{{result.aqarAddedAt}}</p>
                <h1>{{result.aqarType}}</h1>
                <p>
                  <span class="icon" uk-icon="location"></span>
                  <span>{{result.aqarCity}}</span>
                  ،
                  <span>{{result.aqarRegion}}</span>
                </p>
                <p>
                  <span class="icon" uk-icon="expand"></span>
                  <span>400</span>
                  <span>م<sup>2</sup></span>
                </p>
                <p v-if="result.aqarPrice != null">
                  <span class="icon" uk-icon="tag"></span>
                  <span>{{result.aqarPrice}}</span>
                  <span>ريال</span>
                </p>
                <p v-else>
                  <span class="icon" uk-icon="tag"></span>
                  <span>السعر غير محدد</span>
                </p>
                <p>
                  <a href="#" class="uk-button uk-button-secondary round">
                    عرض التفاصيل
                  </a>
                </p>
              </div>
              <div class="actions">
                <button class="uk-button uk-button-secondary">
                  <span uk-icon="icon: heart; ratio: 1.3"></span>
                </button>
                <button class="uk-button uk-button-secondary">
                  <span uk-icon="icon: comment; ratio: 1.3"></span>
                </button>
                <button class="uk-button uk-button-secondary show-number">
                  <span uk-icon="icon: receiver; ratio: 1.3"></span>
                  <span class="num">966343122412</span>
                </button>
              </div>
              <div class="other">
                <span class="uk-badge">إعلان مدفوع</span>
              </div>
            </div>
          </div>
        </div>
        <div class="uk-width-1-1 uk-padding uk-text-center">
          <button class="uk-button uk-button-primary round" @click="more_results">
            عرض المزيد
          </button>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import axios from "axios";
  window.axios = require('axios');

  export default {
    name: "SearchResults",
    created: function () {
      axios.get('http://ihabsalman-001-site9.itempurl.com/adsOrReq/all?pageNumber=1&pageSize=' + this.basicResultsNum + '&lng=ar&=')
      .then(response => (this.results = response.data))
    },
    methods: {
      more_results () {
        this.basicResultsNum += 2,
        axios.get('http://ihabsalman-001-site9.itempurl.com/adsOrReq/all?pageNumber=1&pageSize=' + this.basicResultsNum + '&lng=ar&=')
      .then(response => (this.results = response.data))
      }
    },
    data: function () {
      return {
        basicResultsNum: 2,
        results: null
      }
    },
  }
</script>